//// svg    ''
// import doFnElemVisible from './doFnElemVisible'

module.exports = (container, bar, attr) => {
    //////////////
    let doFnElemVisible = require('./doFnElemVisible');
    let skill = [...document.querySelectorAll('.' + container)]; //     data-pct
    let svgCircles = [...document.querySelectorAll('.' + bar)]; //  
    let percent = []; //     html  -        backend
    let currentCircle; //   
    //,    
    let handleClick = () => {
        skill.forEach((item, i) => {
            percent[i] = parseInt(item.getAttribute(attr)); //      number
            console.log(percent[i])
            currentCircle = item.getElementsByClassName(bar); //    
            currentCircle[0].style.strokeDashoffset = ((100 - percent[i]) / 100) * Math.PI * 180; //         svg  
        })
    }
    if (skill && svgCircles) {
        //    
        console.log('in skillProggressInit')
        svgCircles.forEach(i => {
            i.style.strokeDashoffset = Math.PI * 180;
        })
        // doFnElemVisible('skills', handleClick);
        doFnElemVisible({
            elemClass: 'skills',
            fn: handleClick
        });
    }
    ////////////////////
};


