<template lang="pug">
  .info
    include ../pug/config/mixins
    .info__links
        ul.info-pages
            //- +listItem(' ', 'info-pages', '/works')
            //- +listItem(' ', 'info-pages', '/about')
            //- +listItem('', 'info-pages', 'blog.html')
            //- +listItem('', 'info-pages', 'index.html')
            router-link(
               v-for="(page, index) in pages"
               :key="index"
               tag="li"
               :to="page.route"
               exact
             ).info-pages__item
              a.info-pages__link {{page.name}}
        ul.info-social
            +listItem('', 'info-social', 'https://vk.com/id_r0mashka', 'vk_round')
            +listItem('', 'info-social', 'https://github.com/RomaTur', 'github_round')
            +listItem('', 'info-social', 'https://twitter.com/RoMan_111', 'in_round')
    .info__desc
        .info__about  -  ,             LoftSchool. 
        .info__credits    |     LoftSchool | 2017

</template>

<script>
export default {
  data: () => ({
    pages: [
      { name: ' ', route: '/works' },
      { name: '', route: '/blog' },
      { name: ' ', route: '/about' },
      { name: '', route: '/' },
    ]
  })
}
</script>

<style lang="sass" src='../sass/layout/adjustment.sass' scoped></style>




